<template>
    <section-block title="Мои услуги" :buttons="[{name: 'Добавить услугу', url: '/services/create', icon: 'fa-plus-square-o'}]" :class="[currentService ? 'mess-hidden': 'mess-visible']">
        <div class="service" v-for="(service, key) in services" :class="{active: currentService == key}" @click="currentService = key">
            <div class="service-budget pull-right">
                <span>
                    <template v-if="service.budget">{{service.budget}} ₽</template>
                    <template v-else>Не указан</template>
                </span>
            </div>
            <div class="service-title">{{service.title}}</div>
            <div class="service-description">{{service.description}}</div>
        </div>
    </section-block>
</template>

<script>
    import sectionBlock from '../global/section.vue'

    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'servicesList',
        props: ['selected'],
        data() {
            return {
                currentService: 0,
                services: {
                    1: {
                        id: 1,
                        title: 'Тест',
                        description: 'Тестирование описания желания',
                        budget: 1000,
                    },
                    2: {
                        id: 1,
                        title: 'Тестирование',
                        description: 'Тестирование описания желания, очень объемного описания, которое должно выходить за пределы блока. Тестирование.',
                        budget: 0,
                    },
                    3: {
                        id: 1,
                        title: 'Тестовое желание',
                        description: 'Тестирование описания желания',
                        budget: 3000,
                    },
                }
            }
        },
        components: {
            sectionBlock
        },
        computed: {
            ...mapState(['auth']),
            ...mapGetters(['fullname'])
        },
        methods: {

        },
        watch: {
            selected(id) {
                this.currentService = id;
            },
            currentService(id) {
                this.$emit('setService', id);
            }
        }
    }
</script>

<style lang="scss">
.service {
    padding: 10px;
    border-bottom: 1px solid #f1f1f1;
    cursor: pointer;
    transition: background-color .3s;

    &:hover {
         background-color: #fafafa;
    }

    &.active {
         background-color: aliceblue;
    }

    .service-title {
        color: #3c8dbc;
        font-size: 16px;
        font-weight: 400;
        line-height: 18px;
    }
    .service-description {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        margin-top: 10px;
    }
    .service-budget {
        color: white;
        font-weight: 400;
        background-color: #3c8dbc;
        border-radius: 3px;
        padding: 2px 5px;
    }
}
</style>


